@import "../../utils/commons.scss";

.container {
  .heading {
    padding: 4rem 0;
    position: relative;
    span {
      width: 100%;
      height: 2px;
      background: black;
      margin: 0 auto;
      display: block;
    }
    h3 {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate3d(-50%, -50%, 0);
      font-size: 1.5rem;
      font-weight: 700;
      z-index: 1;
      background: white;
      padding: 0 1rem;
      text-align: center;
      margin: 0;
    }
  }

  .services {
    @extend .flexRow;
    flex-wrap: wrap;
    justify-content: space-between;
    background: var(--clr-primary);
    padding: 0.5rem 0;

    .service {
      @extend .flexRow;
      justify-content: center;
      flex: 1;
      padding: 2rem 0;
      h2 {
        color: white;
        text-align: center;
      }
    }
    .vLine {
      height: 6rem;
      width: 1.5px;
      background: white;
      display: block;
    }
  }

  main {
    width: 100%;
    background: var(--clr-primary-light);
    padding: 2rem 0;
    .wrapper {
      width: var(--fixed-width);
      margin: 0 auto;
    }
    h1,
    p {
      text-align: center;
      color: white;
      span {
        color: white;
      }
    }

    .portfolio {
      margin-top: 2rem;
      .project {
        position: relative;
        img {
          width: 100%;
        }
        span {
          position: absolute;
          bottom: 10px;
          left: 10px;
          color: white;
          font-size: 2rem;
          cursor: pointer;
        }
      }
    }
  }

  @media screen and (max-width: 700px) {
    .heading {
      h3 {
        font-size: 1.2rem;
      }
    }
  }
}
